



























































#mainDiv{
	width: 100%;
	height: 100%;
}
#content {
	height: 100%;
  padding: 10px;
}

#code {
	color: #333;
	font-family: "Consolas", "Monaco", "Bitstream Vera Sans Mono", "Courier New", "sans-serif";
	font-size: 12px;
}

#code .string{
	color: #2a36ff;
}

#code .keyword{
	color: #7f0055;
	font-weight:bold;
}

#code .placeholder{
	margin-left:15px;
}

#code .space{
	margin-left:7px;
}

#code .comments{
	color: #3f7f5f;
}

#errorMsg{
	width: 100%;
	text-align: center;
	font-size: 24px;
	position: absolute;
	top: 100px;
	left:0px;
}
.bottom {
    position:fixed;
    bottom: 0px;
    width: 100%;
    text-align:center;
    padding:10px;
    font-size: 30px;
}



